#usuario-grid{
  display: grid;
  grid-template-areas: "titulo titulo"
  "nombre-usuario tab-usuario"
  "card-usuario tab-usuario"
  "carrito proceso-compra"
  ;
  grid-template-rows: 60px 60px 300px 300px;
  grid-template-columns: 36vw 58vw;
  grid-column-gap: 10px;
  grid-row-gap: 10px;
  align-content: center;
}
#usuario-grid>h2.seccion {
  grid-area: titulo;
}
#usuario-grid>.name-usuario {
  grid-area: nombre-usuario;
}
#usuario-grid>.usuario {
  grid-area: card-usuario;
}
#usuario-grid>.tab-usuario {
  grid-area: tab-usuario;
}
#usuario-grid>#carrito {
  grid-area: carrito;
}
#usuario-grid>.proceso-de-compra {
  grid-area: proceso-compra;
}


.usuario>img {
  border: rgb(167, 219, 216) ridge 3px;
  width: 150px;
  height: 150px;
}

/* Style the tab */
.tab {
  overflow: hidden;
  border: 1px solid #ccc;
  background-color: #f1f1f1;
  font-family: "Josefin Sans", sans-serif;
  line-height: 1.4;
  font-size: large;
}

/* Style the buttons that are used to open the tab content */
.tab button {
  background-color: inherit;
  float: left;
  border: none;
  outline: none;
  cursor: pointer;
  padding: 14px 16px;
  transition: 0.3s;
  font-size: 17px;
}

/* Change background color of buttons on hover */
.tab button:hover {
  background-color: #ddd;
}

/* Create an active/current tablink class */
.tab button.active {
  background-color: #ccc;
}

/* Style the tab content */
.tabcontent {
  display: none;
  padding: 6px 12px;
  border: 1px solid #ccc;
  border-top: none;
}

/*fin tab usuario*/

